<template>
  <div>
      <div>
        <router-view/>
      </div>
  </div>
</template>

<script>
export default {
  name: 'Dashboard',

  components: {},

  mounted() {
  },

  data () {
    return {
      ready: true
    }
  },

  computed: {
    metrics() {
      return this.$store.state.metrics
    },
    queryMetrics() {
      return this.$store.state.metrics.filter(metric => {
        return metric.metrictype == 'Query'
      })
    }
  },

  methods: {

    // not needed anymore
    startApp() {
      this.ready = true
    },

    test() {
      console.log(this.metrics)
    }

  }
}
</script>

